<template>
    <div class="login">
        <img src="../../assets/set/login_bg.jpg" alt="" width="100%">
        <div class="warp">
            <div class="userid inputcss">
                <input type="text" v-model="userID" placeholder="" @keydown='keyToLogin($event)'>
            </div>
            <div class="pwd inputcss">
                <input type="password" v-model="pwd" placeholder="" @keydown='keyToLogin($event)'>
            </div>
            <div class="loginBtn btn">
                <a href="javascript:;" @click="login()"></a>
            </div>
            <div class="regBtn btn">
                <router-link to="/register"></router-link>
            </div>
            <div class="copyRight">
                <p></p>
                <p>Copyright@2017-2018 Sinkhole</p>
                <p>All Right Reserved</p>
            </div>
        </div>
        
    </div>
</template>

<script>
import xsCell from '@/components/design/cell'

import state from 'vuex'
import {mapState} from 'vuex'

import { Toast, Indicator } from 'mint-ui';

export default {
    data(){
        return{
            userID:'',
            pwd:''
        }
    },
    components:{ xsCell },
    methods:{
        keyToLogin: function (ev) {
            //console.log(ev.keyCode)
            //- 
            if(ev.keyCode == 13) this.login();
        },
        login: function() {
            if(this.userID === '' || this.pwd === ''){
                Toast({ message: '', duration: 1500 });
            }
            else{
                Indicator.open({
                    text: '...',
                    spinnerType: 'triple-bounce'
                });
                this.$http({
                    method: "post",
                    url: "http://"+ this.serverURL +"/login",
                    data: {
                        userID: this.userID,
                        pwd: this.pwd
                    }
                }).then(response => {
                    //;
                    if(response.data instanceof Array) {
                        Indicator.close();
                        
                        this.$store.commit('setLoginUserData', response.data[0]);
                        Toast({ message: this.loginUser+' !', duration: 1500 });

                        setTimeout(() => {
                            //- Array 
                            this.$store.commit('login');
                            this.$router.push('/');
                        }, 2000);
                    }else{
                        Indicator.close();
                        //- String  
                        Toast({ message: response.data, duration: 2000 });
                    }
                }).catch(error => {
                    console.log(error);
                });
           }
        }
    },
    computed:{
        ...mapState({
            loginUser: state => state.nowLoginUserData.name,
            islogin: 'islogin',
            serverURL: 'serverURL'
        })
    },
    mounted:function(){
        this.$store.commit('btBackView', {boo: false});
    }
}


</script>

<style scoped>
.login{ 
    width: 100%;
    height: 100%;
    /* background-image: url('../static/images/login_bg.jpg') no-repeat; */
    background-size:100% 100%;
}

.warp{
    width: 100%;
    height: 300px;
    position: fixed;
    bottom: 80px;
    padding-top: 30px;
}
.btn{
    width: 50%;
    height: 45px;
    background: #dfc594;
    color: #000;
    text-align: center;
    line-height: 45px;
    border-radius: 3px;
    margin: 0 auto;
}
.btn a{
    text-decoration: none;
    color: #000;
    font-weight: bold;
    display: block;
    width: 100%;
    height: 45px;
}

.loginBtn{
   position: fixed;
   bottom: 225px;
   left: 25%;
}
.regBtn{
    position: fixed;
   bottom: 160px;
   left: 25%;
}

.inputcss{
    width: 50%;
    height: 40px;
    margin: 0 auto;

}
.inputcss input{
    width: 100%;
    height: 30px;
    border-radius: 5px;
    padding-left: 5px;
    opacity: 0.8
}
.copyRight{
    min-width: 50%;
    position: fixed;
    left: 25%;
    bottom: 80px;
    color: rgb(223, 223, 223);
    font-size: 10px;
    line-height: 16px
}
</style>























